
import { defineComponent } from 'vue'
import { useStore } from 'vuex'
import type { Header, Item, ServerOptions } from 'vue3-easy-data-table'
import { IProductTableItem, IResponseTable } from '@/types'
import PageCard from '@/components/PageCard.vue'
import useFetching, { FetchStatus } from '@/hooks/useFetching'
import useConfirm from '@/hooks/useConfirm'
import ImageTest from '@/assets/images/mock/test.jpg'

export interface IData {
  headers: Header[];
  itemsSelected: Item[];
  serverOptions: ServerOptions;
  noImage: string;
}

export default defineComponent({
  setup () {
    const store = useStore()
    const [loadTable, status, error] = useFetching((payload) => {
      return store.dispatch('product/loadTable', payload)
    })

    const [deleteRows, deleteStatus, deleteError] = useFetching((ids: number[]) => {
      return store.dispatch('product/deleteRows', ids)
    })

    return {
      loadTable,
      status,
      error,
      deleteRows,
      deleteStatus,
      deleteError
    }
  },

  components: {
    PageCard
  },

  computed: {
    isLoading (): boolean {
      return this.status === FetchStatus.pending
    },

    dataTable (): IResponseTable<IProductTableItem> {
      return this.$store.getters['product/getDataTable']
    }
  },

  watch: {
    serverOptions: {
      handler: function (value) {
        this.loadServerSide(value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      headers: [
        { text: 'id', value: 'id', sortable: true },
        { text: 'Изображение', value: 'image' },
        { text: 'Название', value: 'name', sortable: true },
        { text: 'Модель', value: 'model', sortable: true },
        { text: 'Цена на сайте', value: 'price', sortable: true },
        { text: 'Количество', value: 'amount', sortable: true },
        { text: 'Статус', value: 'isActive' },
        { text: 'Действие', value: 'action', width: 60 }
      ],
      itemsSelected: [],
      serverOptions: {
        page: 1,
        rowsPerPage: 5,
        sortBy: 'id',
        sortType: 'desc'
      },
      noImage: ImageTest
    }
  },

  mounted () {
    this.loadServerSide(this.serverOptions)
  },

  methods: {
    handleCreate () {
      this.$router.push({ name: 'product-create' })
    },

    handleUpdate (item: IProductTableItem) {
      this.$router.push({ name: 'product-update', params: { id: item.id } })
    },

    async handleDelete () {
      const dialog = useConfirm({
        title: 'Удаление атрибутов',
        message: 'Вы действительно хотите удалить выбранную группу атрибутов?',
        onConfirm: async () => {
          await this.deleteRows(this.itemsSelected.map((item) => item.id))
          this.itemsSelected = []
          await this.loadServerSide(this.serverOptions)
        }
      })

      await dialog.reveal()
    },

    loadServerSide (options: ServerOptions) {
      const payload: any = {
        limit: options.rowsPerPage,
        offset: (options.page - 1) * options.rowsPerPage
      }

      if (options.sortBy && options.sortType) {
        payload.order = `${options.sortBy}:${options.sortType}`
      }

      this.loadTable(payload)
    }
  }
})
